.projects {
  background: radial-gradient($ghost_white, $daisy_bush);
  padding: 1rem;
}
.projects__header {
  color: $white;
  @extend %myHeader;
}
.projects__text {
  color: $white;
  @extend %textBelowHeader;
}
.projects__list {
  width: 90%;
  display: block;
  margin: 0 auto;
  text-align: center;
  box-shadow: 2px 2px 15px rgba(0, 0 , 0, 0.3);
  padding: 1rem;
  background-color: $daisy_bush;
}
.projects__link {
  display: inline-block;
  color: $thunder;
  text-decoration: none;
  font-size: 2rem;
  background-color: $white;
  margin: 0.6rem 0.2rem;
  border-top-left-radius: 0.1rem;
  border-top-right-radius: 0.1rem;
  border-bottom: 0.2rem solid $thunder;
  padding: 0.5rem 1rem;
  border-radius: 0.2rem;
  width: 90%;
}