.skills__header {
  color: $raisin_black;
  @extend %myHeader;
}
.skills__title {
  font-size: 2.5rem;
  color: $thunder;
  text-align: center;
  padding: 1rem;
}
.skills__list {
  width: 100%;
  text-align: center;
  display: block;
  margin: 0 auto;
  line-height: 1.4rem;
}
.skills__item {
  font-size: 1.4rem;
  display: inline-block;
  border-radius: 50px;
  background-color: $pickled_bluewood;
  padding: 0.8rem 1.2rem;
  color: $white;
  font-weight: 300;

}