.experience__header {
  color: $raisin_black;
  @extend %myHeader;
}
.experience__details,
.experience__input {
  display: none;
}
.experience__input:checked + .experience__title + .experience__details {
  display: inherit;
}
.experience__title {
  background-color: $flame;
  color: $white;
  display: block;
  font-size: 2rem;
  padding: 1rem;
  cursor: pointer;
}
.experience__details {
  border: 0.2rem solid $flame;
}
.experience__top {
  display: flex;
  justify-content: space-between;
}
.experience__date {
  font-style: italic;
  font-size: 1.35rem;
  padding: 1rem;
}
.experience__responsability {
  padding: 0.5rem 1rem;
  line-height: 1.2;
  font-size: 1.2rem;
  &:nth-child(odd) {
    background-color: $concrete;
  }
  &:nth-child(even) {
    background-color: rgba(255, 117, 79, 0.25);
  }
}
.cv-font__icon--plus {
  float: right;
  pointer-events: none;
}
.experience__input:checked + .experience__title {
  .cv-font__icon--plus {
    transform: rotateZ(45deg);
    pointer-events: all;
    cursor: pointer;
  }
}