.menu {
  width: 100%;
  background-color: $white;
  padding: 2rem;
  position: relative;
}
.menu__list {
  border: 0.2rem solid $thunder;
  border-radius: 0.2rem;
}
.menu__link {
  display: block;
  padding: 1rem;
  font-size: 2.5rem;
  color: $thunder;
  text-decoration: none;
  border-bottom: 0.1rem solid $thunder;
  &:last-child {
    border-bottom: 0;
  }
}
.menu__label {
  padding-left: 2rem;
}
.menu__button {
  background: $raisin_black;
  font-size: 3rem;
  color: $white;
  padding: 1rem;
  position: fixed;
  bottom: -15px;
  right: 0;
  border-radius: 0.3rem;
  text-decoration: none;
}