.landscape,
.landscape__input {
  display: none;
}
.landscape {
  position: fixed;
  top: 0;
  width: 100vw;
  height: 100vh;
  background: radial-gradient($peach-orange, $blumine);
  transition: 0.25s;
}
.landscape__button {
  display: block;
  background-color: $pickled_bluewood;
  color: $white;
  position: fixed;
  top: 1rem;
  right: 1rem;
  font-size: 1.5rem;
  padding: 1rem;
  border-radius: 0.5rem;
  text-decoration: none;
  border: 0.1rem solid $white;
}
.landscape__description {
  padding: 1rem 0;
}
@media (orientation: landscape) {
  .landscape {
    color: $white;
    display:flex;
    align-items: center;
    justify-content: center;
    background: radial-gradient($peach-orange, $blumine);
    transition: 0.25s;
  }
  .landscape__list {
    list-style-type: disc;
  }
  .landscape__message--show {
    display: none;
  }
  .landscape__message--hide {
    display: block;
  }
  #landscape:checked + .landscape {
    top: -100%;
    .landscape__message--show {
      display: block;
    }
    .landscape__message--hide {
      display: none;
    }
  }
}