@font-face {
  font-family: 'cv-font';
  src:
    url('asset/font/cv-font.ttf') format('truetype'),
    url('asset/font/cv-font.woff') format('woff'),
    url('asset/font/cv-font.svg') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="cv-font__icon--"], [class*=" cv-font__icon--"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'cv-font' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

